<script>
   /****************************************************************
   * SensitivityPlot                                               *
   * --------------------                                          *
   * shows plot with sensitivity values vs. number of PCs          *
   *****************************************************************/

   import { Vector } from 'mdatools/arrays';
   import { Axes, XAxis, YAxis, Box, Lines, Segments, Rectangles, Points } from 'svelte-plots-basic/2d';

   export let params;                     // JSON with main plot parameters
   export let simcares;                   // JSON with SIMCA results
   export let simca;                      // JSON with SIMCA parameters


   /*****************************************/
   /* Component code                        */
   /*****************************************/

   // constant plot parameters
   const margins = [0.85, 1.0, 0.25, 0.25];
   const showGrid = true;                  // logical, show grid or not
   const xLabel = 'Number of PCs';
   const yLabel = 'Sensitivity';
   const title = 'Sensitivity plot';


   // local copies of plot parameters
   let resID = simcares ? Object.keys(simcares)[0] : '';
   $: if (params && params.resID !== resID) resID = params.resID;

   // select object with current results
   $: resobj = simcares && resID ? simcares[resID] : null;

   $: a = simca ? simca.ncomp : null;
   $: y = resobj ? resobj.v.map(v => v.stat.sensitivity) : null;
   $: x = y ? Vector.seq(1, y.length) : null;

   $: aline = simca ? 1 - simca.alpha/100 : null;
   $: amargin = simca && resobj ? 2 * Math.sqrt(simca.alpha/100 * (1 - simca.alpha/100) / resobj.nrows) : null
   $: atop = amargin + aline;
   $: abottom = aline - amargin;

</script>

{#if params, x, y }
<Axes {margins} {title} {xLabel} {yLabel}
   limX={[0, x.length + 1]}
   limY={[-0.1, 1.1]}>

   <!-- area with possible sensitivity -->
   <Rectangles left={[0]} width={[x.length + 1]} top={[atop]} height={[2 * amargin]} faceColor='#6DE6F460'/>
   <Segments xStart={[0]} xEnd={[x.length + 1]} yStart={[aline]} yEnd={[aline]} lineColor='#10BBE5' lineType={2}/>
   <Segments xStart={[0]} xEnd={[x.length + 1]} yStart={[abottom]} yEnd={[abottom]} lineColor='#10BBE5' lineType={3}/>
   <Segments xStart={[0]} xEnd={[x.length + 1]} yStart={[atop]} yEnd={[atop]} lineColor='#10BBE5' lineType={3}/>

   <!-- data points -->
   <Lines xValues={x} yValues={y} lineWidth={1.5} lineColor='#2679B2' />
   <Points xValues={x} yValues={y} borderWidth={1.5} faceColor='#d3f4f9' borderColor='#2679B2'/>

   <!-- selected component -->
   <Segments xStart={[a]} xEnd={[a]} yStart={[-1]} yEnd={[1.1]} lineColor='#d0d0d0' />
   <Points xValues={[x.v[a - 1]]} yValues={[y[a - 1]]} markerSize={1.5} borderWidth={1.5} faceColor='#2679B2' borderColor='#d0d0d0'/>


   <XAxis {showGrid} slot="xaxis" />
   <YAxis {showGrid} slot="yaxis" />
   <Box slot="box" />
</Axes>
{/if}